function boundsChanged(bounds){
    if(bounds == null){
        return;
    }
    console.log(laroute.action('api::initiatives', {boundaries: JSON.stringify(bounds)}));
    $.ajax({
        type: "get",
        url: laroute.action('api::initiatives', {boundaries: JSON.stringify(bounds)}),
        // data: {country:country},
        success: function(result){
            console.log(result);
        },
        error: function(result){
            console.log(result);
        }
    });
}